@keyframes waiting {
  0% {
    opacity: 0;
  }
  35%, 75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes joltBG {
  0% {
    opacity: 0.3;
  }
  22%, 26% {
    opacity: 0.2;
  }
  27%, 45% {
    opacity: 0.4;
  }
  46%, 76% {
    opacity: 0.5;
  }
  76%, 78% {
    opacity: 0.05;
  }
  78% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.3;
  }
}
.console-output {
  background: #141e1e;
  color: #dce6e6;
  margin: 0 auto;
  font-family: Monaco, Consolas, "Lucida Console", monospace;
  padding: 1em;
  line-height: 1.45;
  position: relative;
  overflow: auto;
  display: flex;
  flex-direction: column-reverse;
}
.console-output p {
  margin: 0;
}
.console-output p.left {
  text-align: left;
}
.console-output p.right {
  text-align: right;
}
.console-output p.center {
  text-align: center;
}
.console-output::selection {
  color: #141e1e;
  background: #dce6e6;
}